import React from 'react';
import {Component} from 'react';
import './App.css';
import './Home.css';
import { ReactComponent } from 'react-router-dom';


class Api extends React.Component {

  constructor(props){
    super(props);
    this.state={
        user:[],
        tmp:''
    }
   this.srch=this.srch.bind(this); 
    this.search=this.search.bind(this) ;
     this.getall=this.getall.bind(this) ;
  }
  del(id){
    fetch('http://localhost/reactnew/delete/'+id)
    .then((res)=>res.json())
    .then((data)=>{
      this.setState({user:data})
    })
    alert("successfully deleted")
  }
  search(){
    fetch('http://localhost/reactnew/srchapi/'+this.state.tmp)
    .then((res)=>res.json())
      .then((data)=>{
        this.setState({user:data})
      })
  }

  srch(e){
    this.setState({tmp:e.target.value});

  }
  getall(){
    fetch('http://localhost/reactnew/getapi')
      .then((res)=>res.json())
      .then((data)=>{
        this.setState({user:data})
      })
  }
  componentDidMount(){
      fetch('http://localhost/reactnew/getapi')
      .then((res)=>res.json())
      .then((data)=>{
        this.setState({user:data})
      })
  }
  
  
  render() {

    return(
      <div>
          <div className="container"> 
          <input type="text" name="srch" value={this.state.tmp} onChange={this.srch}/>
          <input type="button" value="Search" onClick={this.search} className="btn btn-dark"/>
          <input type="button" value="All" onClick={this.getall} className="btn btn-sucsess"/>  
            <table className="table table-striped">
                <thead>
                <tr>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Phone</th>
                    <th>Image</th>
                    <th>Delete</th>

                </tr>
                </thead>
                <tbody>
                {this.state.user.map((u)=>
                <tr key={u.id}>
                    <td>{u.name}</td>
                    <td>{u.email}</td>
                    <td>{u.phone}</td>
                    <td><img src={u.image} className="imgs"/></td>
                    <td><input type="button" value="delete" className="btn btn-danger" onClick={this.del.bind(this,u.id)}/></td>
                </tr>
                )}
                </tbody>
            </table>
            </div>
           <div className="modal" id="myModal">
  <div className="modal-dialog">
    <div className="modal-content">

      
      <div className="modal-header">
        <h4 className="modal-title">Modal Heading</h4>
        <button type="button" className="close" data-dismiss="modal">&times;</button>
      </div>

      
      <div className="modal-body">
        Modal body..
      </div>

      
      <div className="modal-footer">
        <button type="button" className="btn btn-danger" data-dismiss="modal">Close</button>
      </div>

    </div>
  </div>
</div>
     
       </div>
      )
   
  }
}

export default Api;
