import React from 'react';
import {Component} from 'react';
import './App.css';
import './Home.css';
import { withRouter } from 'react-router-dom';

class Home extends React.Component {

  constructor(props){
    super(props);
    this.state={
      name:'',
      email:'',
      phone:'',
      fl:null
    }
    this.cname=this.cname.bind(this);
    this.cemail=this.cemail.bind(this);
    this.cphone=this.cphone.bind(this);
    this.cpic=this.cpic.bind(this);
    this.save=this.save.bind(this)
  }
  cname(ev){
    this.setState({name:ev.target.value})
  }
  cemail(ev){
    this.setState({email:ev.target.value})
  }
  cphone(ev){
    this.setState({phone:ev.target.value})
  }
   cpic(ev){
    this.setState({fl:ev.target.files[0]})
  }
  save(){
    var fd=new FormData();
    fd.append("name",this.state.name);
    fd.append("email",this.state.email);
    fd.append("phone",this.state.phone);
    fd.append("propic",this.state.fl,this.state.fl.name);

    fetch("http://localhost/reactnew/ins",{
      method:'post',
      body:fd
    })
    .then((res)=>res.json())
    .then((data)=>{
      this.props.history.push('/api');
    });
    alert("successfull")
  }

  
  render() {

    return(
      <div>
          
          <h1 style={{color: "red"}}>Input Data</h1>
        <div className="form-group">
          <label for="name">Name</label>
          <p><input type="text" className="form-control" id="name"  value={this.state.name} onChange={this.cname}></input></p>
          
          <label for="email">Email</label>
          <p><input type="email" className="form-control" id="email"  value={this.state.email} onChange={this.cemail}></input></p>
         
         <label for="phone">Phone</label>
          <p><input type="text" className="form-control" id="phone"  value={this.state.phone} onChange={this.cphone}></input></p>
          <p>Image</p>
            <p><input type="file" name="propic" onChange={this.cpic}/></p>
          <p><input type="button" className="btn btn-primary" value="save" onClick={this.save}></input></p>
        </div>
       </div>
      )
   
  }
}

export default withRouter(Home);
